import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { toast } from 'react-toastify';
import { useNavigate, Link } from 'react-router-dom';
import { EmailOutlined as IconEmailOutlined } from '@mui/icons-material';
import { Paper, Box, TextField, InputAdornment, Divider, Button, Grid, Typography, useMediaQuery, Stack } from '@mui/material';
import { useTheme } from '@mui/material/styles';

import AuthWrapper from '../AuthWrapper';
import AuthCardWrapper from '../AuthCardWrapper';
import AuthFooter from 'ui-component/cards/AuthFooter';
import { apiGetEmailVerificationCode } from '../../../actions/user';
import LogoImg from '../../../assets/images/logo.png';
import AnimateButton from 'ui-component/extended/AnimateButton';

const ForgotForm = () => {
  const theme = useTheme();
  const matchDownSM = useMediaQuery(theme.breakpoints.down('md'));
  const navigate = useNavigate();
  const [email, setEmail] = useState('');
  const sendEmailFunc = () => {
    if (!email) toast.error('Fügen Sie die E-Mail ein.');
    else {
      apiGetEmailVerificationCode({ email: email })
        .then((res) => {
          localStorage.setItem('dento', res.data.doc.token);
          toast.success('Geben Sie den Bestätigungscode aus Ihrer E-Mail ein!');
          res.status === 200 && navigate('/auth/verify-code');
        })
        .catch((err) => console.log(err));
    }
  };
  const handleKeyDown = (event) => {
    if (event.key === 'Enter') {
      // Handle the Enter key press, e.g. submit the form
      console.log('Enter key pressed');
    }
  };

  return (
    <AuthWrapper>
      <Grid container display="flex" direction="column" justifyContent="flex-end" sx={{ minHeight: '100vh' }}>
        <Grid item xs={12}>
          <Grid container display="flex" justifyContent="center" alignItems="center" sx={{ minHeight: 'calc(100vh - 68px)' }}>
            <Grid item sx={{ m: { xs: 1, sm: 3 }, mb: 0 }}>
              <AuthCardWrapper>
                <Grid container spacing={2} alignItems="center" justifyContent="center">
                  <Grid item sx={{ mb: 3 }}>
                    <Link to="#">
                      <img src={LogoImg} alt="" style={{ width: '20%' }} />
                    </Link>
                  </Grid>
                  <Grid item xs={12}>
                    <Grid container direction={matchDownSM ? 'column-reverse' : 'row'} alignItems="center" justifyContent="center">
                      <Grid item>
                        <Stack alignItems="center" justifyContent="center" spacing={1}>
                          <Typography color={theme.palette.success.green} gutterBottom variant={matchDownSM ? 'h3' : 'h2'}>
                            Passwort vergessen?
                          </Typography>
                        </Stack>
                      </Grid>
                    </Grid>
                  </Grid>
                  <Grid item xs={12}>
                    <TextField
                      fullWidth
                      name="email"
                      label="E-mail"
                      type="text"
                      variant="outlined"
                      margin="normal"
                      value={email}
                      onChange={(e) => setEmail(e.target.value)}
                      InputProps={{
                        startAdornment: (
                          <InputAdornment position="start">
                            <IconEmailOutlined />
                          </InputAdornment>
                        )
                      }}
                    />
                  </Grid>

                  <Box sx={{ mt: 2 }}>
                    <AnimateButton>
                      <Button
                        fullWidth
                        size="large"
                        type="submit"
                        variant="contained"
                        sx={{ backgroundColor: theme.palette.success.green }}
                        onClick={sendEmailFunc}
                      >
                        E-Mail Senden
                      </Button>
                    </AnimateButton>
                  </Box>
                  <Grid item xs={12}>
                    <Divider />
                  </Grid>
                  <Grid item xs={12}>
                    <Grid item container direction="column" alignItems="center" xs={12}>
                      <Typography component={Link} to="/auth/signup" variant="subtitle1" sx={{ textDecoration: 'none' }}>
                        Sie haben kein Konto?
                      </Typography>
                    </Grid>
                  </Grid>
                </Grid>
              </AuthCardWrapper>
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={12} sx={{ m: 3, mt: 1 }}>
          <AuthFooter />
        </Grid>
      </Grid>
    </AuthWrapper>
  );
};

export default ForgotForm;
